import("../public/data/menu.json").then(({ default: response }) => {
  response.menus.forEach((menu) => {
    const card = document.createElement("div");
    card.classList.add("cards");

    const rating = document.createElement("h4");
    rating.textContent = "Rating : " + menu.rating;

    const name = document.createElement("h2");
    name.textContent = menu.name;

    const imageContainer = document.createElement("div");
    imageContainer.classList.add("image-container");

    const image = document.createElement("img");
    image.classList.add("zoom-image");
    image.src = menu.pictureURL;
    image.alt = menu.name;

    imageContainer.appendChild(image);

    const description = document.createElement("p");
    description.textContent = menu.description;

    const main = document.querySelector(".menu");
    card.appendChild(imageContainer);
    card.appendChild(rating);
    card.appendChild(name);
    card.appendChild(description);
    main.appendChild(card);
  });
});
